<template>
    <div class="content container">
        <h1 class="text-center header"><i18n path="common.changeLog"></i18n></h1>
        <section v-for="(cl, index) in changeLogs" :key="index" class="mb-4">
            <h3 class="px-3">v{{cl.version}} <small class="ml-2">{{cl.time}}</small></h3>
            <p v-for="(log, logIndex) in cl.changes" :key="logIndex" v-html="log"></p>
        </section>
    </div>
</template>

<script>
    import { getProjectByKey } from "@/store/urls";

	export default {
        data(){
            return {
                changeLogs: []
            };
        },
        methods: {
            loadChangeLog(){
				const s = this.$store.state, project = this.$route.params.project;
                if(project && s.language && s.changeLog) this.changeLogs = s.changeLog[project][s.language];
            }
        },
        watch:{
            '$store.state.language'(){
                this.loadChangeLog();
            }
        },
		beforeMount(){
            this.loadChangeLog();
        }
    }
</script>

<style lang="scss" scoped>
    .content{
        background-color: white;
        width: 800px;
        padding-bottom: 30px;
        .header {
            margin: 100px 0 60px;
        }
        .jumbotron{
            background-color: white;
            padding-left: 0;
            margin-bottom: 10px;
            h1 { font-size: 50px;font-weight: lighter; }
        }
        h3{
            color: white;
            background-color: #41B883;
            border-radius: 3px;
            padding: 3px 5px;
            display: inline-block;
            font-size: 1.5rem;
            small{ color: green; }
        }
        .desc{
            font-size: 15px;
            color: #666666;
            margin: 0 50px 20px 50px;
        }
        p{
            margin: 0 0 5px 60px;
            font-size: 16px;
            display: list-item;
            list-style: disc;
        }
    }
</style>